*{
  margin: 0;
  padding: 0;
}

/* Style for the entire login form container */
.login-form {
  max-width: 400px;
  margin: 0 auto;
  padding: 20px;
  background-color: #f2f2f2;
  border-radius: 5px;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
  margin-top: 10%;
}

/* Style for the form header */
.login-form h2 {
  font-size: 24px;
  margin-bottom: 20px;
  color: #333;
}

/* Style for labels */
.login-form label {
  display: block;
  font-weight: bold;
  margin-bottom: 5px;
  color: #555;
}

/* Style for input fields */
.login-form input[type="email"],
.login-form input[type="password"] {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 3px;
  font-size: 16px;
}

/* Style for the submit button */
.login-form button[type="submit"] {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  font-size: 18px;
  cursor: pointer;
}

.login-form button[type="submit"]:hover {
  background-color: #0056b3;
}

/* Style for the "Create a New Account" link */
.login-form a {
  display: block;
  margin-top: 15px;
  color: #007bff;
  text-decoration: none;
}

.login-form a:hover {
  text-decoration: underline;
}



/* Styles for the register-form container */
.register-form {
  max-width: 400px;
  margin: 0 auto;
  margin-top: 5%;
  padding: 20px;
  background-color: #f2f2f2;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: center;
  font-family: Arial, sans-serif;
}

/* Styles for the h2 element */
.register-form h2 {
  color: #333;
}

/* Styles for the form and its labels */
.register-form form {
  text-align: left;
}

.register-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: bold;
}

/* Styles for the input fields */
.register-form input {
  width: 100%;
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

/* Styles for the submit button */
.register-form button[type="submit"] {
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 3px;
  padding: 10px 20px;
  cursor: pointer;
}

.register-form button[type="submit"]:hover {
  background-color: #0056b3;
}

/* Styles for the link */
.register-form a {
  display: block;
  margin-top: 10px;
  text-decoration: none;
  color: #007bff;
}

.register-form a:hover {
  text-decoration: underline;
}
